@charset "UTF-8";
.movie-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 30px;
}

.movie-header h1 {
  color: #4692ff;
}

.movie-wrapper {
  padding: 30px 0 50px;
}

.movie-wrapper .movie-loading {
  color: #fff;
}

.movie-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 30px;
}

.movie-list li {
  width: 100%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.movie-list a {
  display: block;
  position: relative;
  padding: 16px;
  border-radius: 16px;
  background: #363636;
  color: #f3f3f3;
}

.movie-list .image {
  position: relative;
  width: 100%;
  height: 260px;
  background: #222;
  border-radius: 12px;
  overflow: hidden;
}

.movie-list .image::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 150px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0.6)));
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6));
}

.movie-list .image img {
  position: absolute;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.movie-list h3 {
  z-index: 1;
  position: absolute;
  bottom: 0;
  padding: 10px 12px;
  font-size: 20px;
}

.movie-list .info {
  margin: 10px 6px;
  font-size: 13px;
  color: #aaa;
}

.movie-list .info-genre {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.movie-list .info-genre span + span::before {
  content: "Â·";
  margin: 0 2px;
}

.movie-list .summary {
  display: -webkit-box;
  height: 88px;
  padding: 0 6px;
  overflow: hidden;
  word-break: break-all;
  text-overflow: ellipsis;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  font-size: 15px;
  line-height: 22px;
}

.movie-list .rating {
  margin: 10px 6px 0;
  font-size: 13px;
}

.movie-list .rating-star {
  display: inline-block;
  width: 15px;
  height: 19px;
  margin-right: 2px;
}

.movie-list .rating-star svg {
  width: 100%;
  height: 19px;
  vertical-align: top;
}

@media screen and (min-width: 600px) {
  .movie-list li {
    width: calc(50% - 15px);
  }
}

@media screen and (min-width: 860px) {
  .movie-list li {
    width: calc(33.3333% - 20px);
  }
}
